import {Switch, Route, Link , withRouter} from 'react-router-dom';
import React, {lazy, Suspense} from 'react';

import Index from 'components/index';
import styles from './styles.module.scss';
import left from './arrow-left-circle.svg';
import right from './arrow-right-circle.svg';

import loadData from 'services/storage';
import MutualFund from 'components/mutual-fund';
import { NextLesson } from 'components/next-lesson';
import EmailSub from 'components/email-sub';
import PlaystoreBanner from 'components/play-store';

const Basics = lazy(() => import('pages/basics'));
const Savings = lazy(() => import('pages/savings'));
const MutualFunds = lazy(() => import('pages/mutual-funds'));

class Post extends React.Component {

    constructor(props){
        super(props);
        this.lessons = [];
        this.state = {
            index: 0,
            tableOpen: false,
            lessonStructure: [],
            nextLesson: null
        }
    }

    updateNav(){
        const pathname = window.location.pathname;
        this.setState({tableOpen: false})
        for(var i=0;i<this.lessons.length;i++){
            if(pathname.startsWith(this.lessons[i].route)){
                this.setState({index:i});
                break;
            }
        }
    }

    componentDidMount(){
        fetch("/lessons.json")
        .then((res)=>res.json())
        .then(res=>{
            loadData().then((_updatedLessons)=>{
                console.log("updated:", _updatedLessons);
                window.localStorage.setItem('lessons', JSON.stringify(_updatedLessons));
                this.setState({lessonStructure: _updatedLessons});


                _updatedLessons.forEach((element,i) => {
                    var l = element["pages"].length;
                    element.pages.forEach((element1,j) => {
                        this.lessons.push({
                            route: element1["route"],
                            title: "L" + (i+1).toString() + " : " + element["title"],
                            subtitle: (j+1).toString() + "/" + l.toString() + " " + element1["title"],
                            title1: element1["title"]
                        })
                    });
                });

                this.updateNav();
                this.props.history.listen(location=>{
                    this.updateNav();
                });
            })
        })
    }

    toggleTable(){
        this.setState({tableOpen: !this.state.tableOpen});
    }

    next=(_nextRoute)=>()=>{
        var _lessonStructure = [...this.state.lessonStructure];
        var nextRoute = null;
        var currentFound=false;
        for(var lesson of _lessonStructure){
            if(nextRoute!=null) break;
            for(var page of lesson.pages){
                console.log(_nextRoute, page.route, this.lessons[this.state.index].route)
                console.log(this.state.index, this.lessons.length)
                if(currentFound){
                    nextRoute=page.route;
                    currentFound=false;
                    break;
                }
                if(this.lessons[this.state.index].route.startsWith(page.route)){
                    page.status="read";
                    currentFound=true;
                    console.log(page.quiz)
                    if(page.quiz){
                        if(!window.location.pathname.endsWith('/quiz')){
                            nextRoute=page.route+'/quiz';
                            break;
                        }
                    }
                }
            }
        }
        window.localStorage.setItem('lessons',JSON.stringify(_lessonStructure));
        this.setState({lessonStructure: _lessonStructure});
        if(_nextRoute==null){
            if(nextRoute!=null)
                this.props.history.push(nextRoute);
            else
                this.props.history.push("/");
        }else{
            this.props.history.push(_nextRoute);
        }

    }

  render(){
    console.log("Here:", this.lessons[this.state.index+1]);
    return (
        <div className={styles['post-page']}>
            <Index lessons={this.state.lessonStructure} open={this.state.tableOpen} click={this.next} toggle={this.toggleTable.bind(this)} next={this.next(null)}/>
            <div className={styles['post-view']}>
                <Switch>
                    <Route path="/post/the-absolute-basics" component={()=>(
                        <Suspense fallback={(<div>Loading...</div>)}><Basics/></Suspense>
                    )}/>
                    <Route path="/post/the-savings-dilemma" component={()=>(
                        <Suspense fallback={(<div>Loading...</div>)}><Savings/></Suspense>
                    )}/>
                    <Route path="/post/wtf-are-mutual-funds" component={()=>(
                        <Suspense fallback={(<div>Loading...</div>)}><MutualFunds/></Suspense>
                    )}/>
                </Switch>
                {this.state.index<this.lessons.length-1 && (
                    <div onClick={this.next(null)} className={styles["next-container"]}>
                        <h3>next lesson</h3>
                        <NextLesson showNext={true} text={this.lessons[this.state.index+1]["title1"]}/>
                    </div>
                )}
                <EmailSub/>
            </div>
            {(this.lessons.length>0) && 
                <div className={styles["post-nav"]}>
                    {this.state.index!=0?
                        <Link to={this.lessons[this.state.index-1].route}><img src={left} alt="left"/></Link>
                    :
                        <img src={left} alt="left"/>
                    }
                    <div className={styles["current-path"]} onClick={this.toggleTable.bind(this)}>
                        <h5>{this.lessons[this.state.index].title}</h5>
                        <h4>{this.lessons[this.state.index].subtitle}</h4>
                    </div>
                    <img src={right} alt="right" onClick={this.next(null)}/>
                </div>
            }
        </div>
    );
  }
}

export default withRouter(Post);