import React from 'react';
import {withRouter} from 'react-router-dom';

import styles from './styles.module.scss';
import tick from './tick.svg';
import arrow from './arrow.svg';

class Index extends React.Component {

    constructor(props){
        super(props);
        this.state = {lessons: props.lessons, collapse: [], offset:0};
        this.nextRef = React.createRef();
    }
    componentDidMount(){
        function onScroll(e){
            console.log("Scrol");
            if(this.nextRef.current==null) return;
            var offset= this.nextRef.current.getBoundingClientRect().top-160;
            this.setState({offset: offset<0?-offset:0});
        }
        onScroll = onScroll.bind(this);
        window.addEventListener('scroll', onScroll);
        this.cleanUp = ()=>{
            window.removeEventListener('scroll', onScroll);
        }
    }

    componentWillUnmount(){
        console.log("Ok");
        this.cleanUp();
    }

    componentDidUpdate(prevProps, prevState){
        if(prevProps!=this.props){
            this.setState({lessons: this.props.lessons});
        }
    }

    collapse = (i)=>()=>{
        var collapse = [...this.state.collapse];
        var index = collapse.indexOf(i);
        if (index > -1) {
            collapse.splice(index, 1);
            this.setState({collapse});
        }else{
            this.setState({collapse: [...collapse, i]});
        }
    }

    render(){
        var lessonComponents = [];
        var nextLesson = null;
        var foundCurrent = false;
        this.props.lessons.forEach((lesson,i) => {
            var pageComponents = [];
            lesson.pages.forEach((page,j)=>{
                var style={};
                var dotStyle={};
                var lineHeight=33;
                if(j==0){
                    style={...style, paddingTop: "0px"};
                    dotStyle={...dotStyle, top: "3px"};
                }
                if(j==(lesson.pages.length-1)){
                    lineHeight -= 10;
                }
                var classNames = styles["chapter"];
                if(page.status=='read')
                    classNames += " "+styles["read"];
                if (foundCurrent){
                    nextLesson = {
                        title: page["title"],
                        img: lesson["img"],
                        route: page["route"],
                    };
                    foundCurrent = false;
                }
                if(page.route==this.props.location.pathname){
                    classNames += " "+styles["reading"];
                    foundCurrent = true;
                    if(j!=lesson.pages.length-1)
                        lineHeight += 56;
                }

                pageComponents.push(
                    <div style={style} className={classNames}>
                        <div style={{height: lineHeight}} className={styles["chapter-line"]}></div>
                        <div style={dotStyle} className={styles["chapter-dot"]}>
                            <img src={tick}/>
                        </div>
                        <span onClick={this.props.click(page.route)}>{page.title}</span><br/>
                        <span className={styles["here"]}>👆 You are here</span>
                    </div>
                );
            });
            lessonComponents.push(
                <div className={styles["lesson"] + " " + (this.state.collapse.includes(i)?styles["collapsed"]:"")}>
                    <div className={styles["title"]} onClick={this.collapse(i)}>
                        <div className={styles["dot"]}><div></div></div>
                        <b><span>L{i+1} : {lesson.title}</span></b>
                        <img src={arrow} className={styles["arrow"]}/>
                    </div>
                    <div className={styles["bend"]}>
                        <div className={styles["left"]}></div>
                        <div className={styles["right"]}></div>
                    </div>
                    <div className={styles["chapters"]}>
                        {pageComponents}
                    </div>
                </div>
            )
        });

        return (
            <div className={styles["index"] + " " + (this.props.open?"":styles["closed"])}>
                <div className={styles["index-container"]}>
                    <div className={styles["header"]}>
                        <div className={styles["title-box"]}>
                        <div className={styles["container"]}>
                            <h3 className={styles["title"]}>Your progress so far</h3>
                            <div className={styles["title-line"]}></div>
                        </div>
                        </div>
                    </div>
                    <div className={styles["list"]}>
                        {lessonComponents}
                    </div>
                </div>
                { nextLesson && (
                    <div ref={this.nextRef}>
                        <div className={styles["next-lesson"]} style={{"top": this.state.offset}} onClick={this.props.click(nextLesson["route"])}>
                            <h2>next lesson</h2>
                            <div>
                                <img src={nextLesson["img"]}/>
                                <h1>{nextLesson["title"]}</h1>
                            </div>
                        </div>  
                    </div>
                )}
                <div className={styles["nav"]}>
                    <div className={styles["back"]} onClick={this.props.toggle}>BACK</div>
                    <div className={styles["next"]} onClick={this.props.next}>NEXT LESSON</div>
                </div>
            </div>
        );
    }
}

export default withRouter(Index);