import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import styles from './styles.module.scss';

import savemoLogo from './savemowhite.svg';
import slash from './slash.svg';
import pfm from './pfmwtf.svg';
import ham from './ham.svg';

class NavBar extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            open: false
        }
        this.props.history.listen(location=>{
            this.setState({open: false});
        });
    }

    toggle=()=>{
        this.setState({open: !this.state.open});
    }

    render(){
        return (
            <div className={styles["nav-bar"]}>
                <div className={styles["header"]}>
                        <div>
                            <Link to="/"><img src={pfm} /></Link>
                            by
                            <a target="_blank" href="https://www.savemo.app"><img src={savemoLogo} /></a>
                        </div>
                    <img src={ham} onClick={this.toggle} />
                </div>
                <div className={styles["menu"] + " " + (this.state.open?styles["open"]:"")}>
                    <Link to="/">Home</Link>
                    <a target="_blank" href="https://www.savemo.app">Savemo</a>
                    <a target="_blank" href="https://tax.savemo.app">Tax Calculator</a>
                    <a target="_blank" href="https://www.instagram.com/savemo.india/">Instagram</a>
                    <a target="_blank" href="https://twitter.com/savemo_app">Twitter</a>
                </div>
            </div>
        );
    }
}
NavBar = withRouter(NavBar);
export { NavBar };