import { Component } from 'react';
import styles from './styles.module.scss';
import badge from './google-play-badge.png';

class PlaystoreBanner extends Component{
    render(){
        return (
            <div className={styles["playstore-badge"]}>
                <p className={styles["badge"]}>{this.props.text}</p>
                <a href="https://play.google.com/store/apps/details?id=app.savemo.savemo" target="_blank"><img className={styles["badge"]} src={badge}/></a>
            </div>
        )
    }
}

export default PlaystoreBanner;