import styles from './styles.module.scss';

import logo from './logo.svg';
import android from './android-badge.png';
import apple from './apple-badge.png';
import amfi from './amfi.svg';

export function Footer(){
    return (
        <div className={styles["footer-container"]}>
            <div className={styles["footer"]}>
                <img className={styles["logo"]} src={logo}/>
                <div className={styles["left"]}>
                    <h4>Making money fun again,<br/>one rupee at a time.</h4>
                    <p>We are an AMFI registered<br/>mutual fund distributor</p>
                    <div>
                        <img src={amfi}/>
                        <div className={styles["amfi"]}>
                            <p>Association of Mutual</p>
                            <p>Funds in India</p>
                            <p>ARN-168501</p>
                        </div>
                    </div>
                </div>
                <div className={styles["right"]}>
                    <h1>Start savin’ now 🤙</h1>
                    <div className={styles["get"]}>
                        <a target="_blank" href="https://play.google.com/store/apps/details?id=app.savemo.savemo"><img className={styles["android"]}  src={android}/></a>
                        {/* <img className={styles["apple"]}  src={apple}/> */}
                    </div>
                    <p>Built with ❤️ and lots of ☕️<br/>in God’s Own Country, Kerala</p>
                    <p className={styles["copyright"]}>© 2020 Savemo. T&#38;C</p>
                </div>
            </div>
        </div>
    )
}