import {Link} from 'react-router-dom';
import styles from './style.module.scss';

import next from './open.svg';

export function NextLesson(props){
    console.log(props);
    return(
        <div className={styles["next-lesson"]}>
            {props.route && (
                <Link to={props.route}>
                    {props.text}
                    {props.showNext && (<img src={next}/>)}
                </Link>
            )}
            {props.route==null && (
                <a>
                    {props.text}
                    {props.showNext && (<img src={next}/>)}
                </a>
            )}
        </div>
    )
}