import { Component, createRef } from 'react';
import styles from './styles.module.scss';

class EmailSub extends Component{

    constructor(props){
        super(props);
        this.inputRef = createRef();
        this.state={
            statusColor: 'transparent',
            status: 'nothing'
        }
    }

    validateEmail(mail)
    {
        return mail.match(/^.+@.+\.[^\.]+$/);
    }

    submit(e){
        e.preventDefault();
        console.log(e);
        var email = this.inputRef.current.value;
        console.log(email);
        if(email == undefined || email ==null) return;
        fetch('https://api.airtable.com/v0/app2xpglvcAPiAeIo/Table%201',{
            method: 'post',
            headers: {
                'Authorization': 'Bearer keyrsEfqNGVcdTX9D',
                'content-type': 'application/json'
            },
            body: JSON.stringify({'records':[{'fields':{'Email ID': email}}]})
        }).then((res)=>{
            console.log(res);
            if(res.status==200){
                this.setState({
                    status: 'You\'re in! <b>The best decision you\'ve ever made</b>',
                    statusColor:  '#008AFC'
                });
            }
            else{
                this.setState({
                    status: 'Sorry! Some technical issue?',
                    statusColor:  'red'
                });
            }
        })
    }

    render(){
        return (
            <div className={styles["email-sub"]}>
                <p>Want to receive product updates?</p>
                <form onSubmit={this.submit.bind(this)}>
                    <input name="email" type="email" placeholder="Enter your email" ref={this.inputRef}></input>
                    <input type="submit" value="I'M IN"></input>
                </form>
                <p style={{color: this.state.statusColor}} dangerouslySetInnerHTML={{__html: this.state.status}}></p>
            </div>
        )
    }
}

export default EmailSub;