function loadData(){
    return new Promise((resolve,reject)=>{
        fetch("/lessons.json")
        .then((res)=>res.json())
        .then(res=>{
            var existingLessonsString = window.localStorage.getItem('lessons');
            var updatedLessons=[];
            res.forEach((element,i) => {
                var l = element["pages"].length;
                var updatedPages = [];
                element.pages.forEach((element1,j) => {
                    var status="unread";
                    updatedPages.push({...element1, status: status})
                });
                updatedLessons.push({...element, pages: updatedPages});
            });
            var _updatedLessons = [];
            if(existingLessonsString!=null){
                var existingLessons = JSON.parse(existingLessonsString);
                console.log('existing:', existingLessons);
                for(var updatedLesson of updatedLessons){
                    var existingLesson=null;
                    for(var _existingLesson of existingLessons){
                        if(_existingLesson['title']==updatedLesson['title']){
                            existingLesson=_existingLesson;
                            break;
                        }
                    }
                    if(existingLesson != null){
                        var _updatedPages = [];
                        for(var updatedPage of updatedLesson.pages){
                            for(var _existingPage of existingLesson.pages){
                                if(_existingPage['title']==updatedPage['title']){
                                    updatedPage['status']=_existingPage['status'];
                                    break;
                                }
                            }
                        }
                        _updatedLessons.push(updatedLesson);
                    }
                }
            }
            else{
                _updatedLessons = updatedLessons;
            }
            resolve(_updatedLessons);
        });
    });
}

export default loadData;