import React from 'react';

import styles from './styles.module.scss';
import check from './check.svg';
import motilal from './amc-images/motilal.png';

var tagColors = {
    "EQUITY": "#003867",
    "INDEX": "#603D1C",
    "ELSS": "#0C4B2D"
}

var tagFontColor = {
    "EQUITY": "#008AFC",
    "INDEX": "#C6AD55",
    "ELSS": "#35B678"
}

class MutualFund extends React.Component {
    render (){
        return(
            <div className={styles["mutual-fund"]} onClick={this.props.onClick}>
                <div className={styles["header"]}>
                    <img className={styles["icon"]} src={motilal}/>
                    <div className={styles["description"]}>
                        <span className={styles["fund-name"]}>Motilal Oswal Nasdaq 100 FOF</span>
                        <div className={styles["tags"]}>
                            <div className={styles["tag"]} style={{background: tagColors["EQUITY"], color: tagFontColor["EQUITY"]}}>EQUITY</div>
                            <div className={styles["tag"]} style={{background: tagColors["INDEX"], color: tagFontColor["INDEX"]}}>INDEX</div>
                            <div className={styles["tag"]} style={{background: tagColors["ELSS"], color: tagFontColor["ELSS"]}}>ELSS</div>
                        </div>
                    </div>
                    <div className={styles["check"] + " " + (this.props.selected?styles["selected"]:"")}>
                        <img src={check}/>
                    </div>
                </div>
                <div className={styles["details"]}>
                    <div className={styles["detail"]}>
                        <div className={styles["label"]}>Returns(3Y)</div>
                        <div className={styles["value"]}>13.23%</div>
                    </div>
                    <div className={styles["detail"]}>
                        <div className={styles["label"]}>Exit Load</div>
                        <div className={styles["value"]}>2%</div>
                    </div>
                    <div className={styles["detail"]}>
                        <div className={styles["label"]}>Expense Ratio</div>
                        <div className={styles["value"]}>0.1%</div>
                    </div>
                </div>
            </div>
        )
    }
}

export default MutualFund;